import React from 'react';
import PropTypes from 'prop-types';

const Header = (props) => {
  const { title } = props;

  return <header className="Header">{title}</header>;
};

Header.propTypes = {
  title: PropTypes.string,
};

Header.defaultPropTypes = {
  title: 'Where in the world?',
};

export default Header;
